@media screen and (min-width: 320px) {
    .header {
        height: 60px;
        background: #000;
        color: #fff;
        display: flex;
        align-items: center;
        position: fixed;
        bottom: 0;
        width: 100%;
    }

    .header__items {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}